
import { defineComponent, ref } from "vue";
import SinglePost from "./single-post.vue";

export default defineComponent({
  name: "PostList",
  props: ["posts"],
  components: { SinglePost },
  setup() {
    const toggle = ref(true);

    const togglePost = () => {
      toggle.value = !toggle.value;
    };

    return { togglePost, toggle };
  },
});
